import EvanForm from '@/components/evan-form/evan-form.vue'
import EvanFormItem from '@/components/evan-form/evan-form-item.vue'
import utils from '@/components/evan-form/utils.js'
export default {
	components: {
		EvanForm,
		EvanFormItem
	},
	data() {
		return {
			what:'',
			hideRequiredAsterisk: false,
			sexes: [{
					name: '男',
					value: 'man'
				},
				{
					name: '女',
					value: 'woman'
				}
			],
			// 表单的内容必须初始化
			info: {
				name: '',
				email: '',
				desc: '',
				phone: '',
				sex: '',
			},
			rules: {
				name: {
					required: true,
					message: '请输入姓名'
				},
				email: [{
					required: true,
					message: '请输入邮箱'
				}, {
					type: 'email',
					message: '邮箱格式不正确'
				}],
				desc: [{
						required: true,
						message: '请输入简介'
					},
					{
						min: 10,
						max: 30,
						message: '简介必须在10到30个字之间'
					}
				],
				phone: [{
						required: true,
						message: '请输入手机号'
					},
					{
						validator: (rule, value, callback) => {
							// 注意这里如果用的是methods里的isMobilePhone将不生效
							if (this.$utils.isMobilePhone(value)) {
								callback()
							} else {
								callback(new Error('手机号格式不正确'))
							}
						}
					},
					// 或者这样也是可以的
					// {
					// 	validator: this.isMobile
					// }
				],
				sex: {
					required: true,
					message: '请选择性别'
				}
			}
		}
	},
	mounted() {
		// 这里必须放在mounted中，不然h5，支付宝小程序等会找不到this.$refs.form
		this.$refs.form.setRules(this.rules)
	},
	methods: {
		save() {
			this.$refs.form.validate((res) => {
				if (res) {
					uni.showToast({
						title: '验证通过'
					})
				}
			})
		},
		utilsSave() {
			utils.validate(this.info, this.rules, (res, errors) => {
				if (res) {
					uni.showToast({
						title: '验证通过'
					})
				}
			})
		},
		validateSingle() {
			this.$refs.form.validateField('email', (res) => {
				if (res) {
					uni.showToast({
						title: '验证通过'
					})
				}
			})
		},
		validateMultiple() {
			this.$refs.form.validateField(['email', 'phone'], (res) => {
				if (res) {
					uni.showToast({
						title: '验证通过'
					})
				}
			})
		},
		hideReqired() {
			this.hideRequiredAsterisk = !this.hideRequiredAsterisk
		},
		isMobilePhone() {
			const reg = /^1\d{10}$/
			if (reg.test(value)) {
				return true
			}
			return false
		},
		isMobile(rule, value, callback) {
			if (this.$utils.isMobilePhone(value)) {
				callback()
			} else {
				callback(new Error('手机号格式不正确'))
			}
		},
		sexChange(e) {
			this.info.sex = e.detail.value
		}
	}
}
